// paginas/alumno.tsx
'use client'; // <-- Este componente es de cliente

import { useSession } from 'next-auth/react'; // Importa useSession
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { FaUserGraduate, FaPen, FaChartBar, FaSignOutAlt, FaInfoCircle } from 'react-icons/fa'; // Icono cambiado para Acerca de


export default function DashboardAlumno() {
  const router = useRouter();
  const { data: session, status } = useSession(); // Usa el hook useSession

  const handleLogout = () => {
    // Aquí puedes usar signOut() de next-auth/react si quieres cerrar sesión de forma segura
    // signOut({ callbackUrl: '/paginas/login' });
    router.push('/paginas/login'); // Esto solo redirige
  };

  // --- Manejo del estado de la sesión ---
  if (status === "loading") {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black text-teal-300 font-mono text-xl">
        Cargando sesión...
      </div>
    );
  }

  // Redirige al login si no está autenticado
  // Opcional: podrías mostrar un mensaje de error y redirigir
  if (status === "unauthenticated") {
    router.push('/paginas/login');
    return null;
  }

  // Extrae el nombre y el ID del usuario de la sesión
  const userName = session?.user?.name || 'Usuario';
  const userId = session?.user?.id; // Asegúrate de que el 'id' esté en el objeto de sesión de NextAuth.js

  // Puedes añadir una verificación de rol aquí también si quieres
  // if (session?.user?.role !== 3) { // Asumiendo que 3 es el rol de alumno
  //   router.push('/unauthorized'); // Redirige a una página de acceso no autorizado
  //   return null;
  // }


  return (
    <div className="min-h-screen flex flex-col bg-black text-teal-300 font-mono">
      {/* Navbar */}
      <nav className="flex flex-wrap items-center justify-between px-6 py-4 bg-gradient-to-r from-neutral-900 to-black border-b border-teal-500/30 shadow-md">
        {/* Título lado izquierdo */}
        <h1 className="flex items-center gap-2 text-xl font-bold text-teal-300 mr-auto">
          <FaUserGraduate className="text-2xl" />
          {userName} (ID: {userId}): {/* Muestra el nombre y el ID */}
        </h1>

        {/* Links centrados en columna */}
        <div className="flex flex-col sm:flex-row sm:items-center gap-4 sm:gap-6 text-sm font-medium">
          <Link href="/paginas/examen" className="flex items-center gap-2 hover:text-teal-100 transition">
            <FaPen />
            Examen
          </Link>
          <Link href="/paginas/resultados" className="flex items-center gap-2 hover:text-teal-100 transition">
            <FaChartBar />
            Resultados
          </Link>
        </div>
        {/* Acerca de - esquina inferior derecha */}
          <div className="absolute bottom-4 right-6">
            <Link
              href="/paginas/acercade"
              className="flex items-center gap-2 text-sm text-teal-400 hover:text-teal-300 transition"
            >
              <FaInfoCircle /> {/* Usando un icono más apropiado para "Acerca de" */}
              Acerca de
            </Link>
          </div>
        {/* Botón cerrar sesión al lado derecho */}
        <button
          onClick={handleLogout}
          className="flex items-center gap-2 text-red-400 hover:text-red-500 transition hover:underline ml-4"
        >
          <FaSignOutAlt className="text-2xl" />
        </button>
      </nav>

      {/* Contenido principal */}
      <main className="flex-1 px-6 py-10 bg-neutral-950">
        <h2 className="text-2xl border-b border-teal-500/30 pb-2 mb-6 font-semibold">Bienvenido al Sistema de Programadores Mexicanos (SPM)</h2>
        <div className="bg-neutral-900 p-6 rounded-xl border border-teal-500/20 shadow-md text-base text-teal-100">
          <p>Realiza los examenes asignados y demuestra tus habilidades</p>
        </div>
      </main>
    </div>
  );
}